.skeleton {
    background-color: #e0ceff;
    border-radius: 4px;
    width: 460px;
    height: 144px;
    position: relative;
    overflow: hidden;
  }
  
  .skeleton::after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 1));
    position: absolute;
    top: 0;
    left: -100%;
    animation: shimmer 3.5s infinite;
  }
  
  @keyframes shimmer {
    50% {
      transform: translateX(100%);
    }
    100% {
        transform: translateX(0%);
      }
  }